import "./Heading.css";

export const createHeading = ({
  size = 1,
  color = color,
  text = "",
  font = "",
}) => {
  const heading = document.createElement(`h${size}`);
  heading.style.color = color;
  heading.innerText = text;
  heading.fontFamily = font;
  heading.className = [`btn--${size}`];

  return heading;
};
